import React from 'react';
import Task from './Task';
import './TaskList.css';

const TaskList = (props) => {
	const active = props.tasks.filter((task) => {
		return task.active;
	});
	const done = props.tasks.filter((task) => {
		return !task.active;
	});
	if (done.length >= 2) {
		done.sort((a, b) => {
			if (a.finishDate < b.finishDate) {
				return 1;
			}
			if (a.finishDate > b.finishDate) {
				return -1;
			}
			return 0;
		});
	}
	if (active.length >= 2) {
		active.sort((a, b) => {
			a = a.text.toLowerCase();
			b = b.text.toLowerCase();
			if (a < b) {
				return -1;
			}
			if (a > b) {
				return 1;
			}
			return 0;
		});
	}
	const activeTasks = active.map((task) => (
		<Task key={task.id} task={task} change={props.change} delete={props.delete} />
	));
	const doneTasks = done.map((task) => (
		<Task key={task.id} task={task} change={props.change} delete={props.delete} />
	));
	return (
		<div>
			<div className="active">
				<h3>
					Lista zadań do wykonania <em>({active.length})</em>
				</h3>
				{activeTasks.length > 0 ? activeTasks : <p>Brak zadań.</p>}
			</div>
			<hr />
			<div className="done">
				<h3>
					Zadania wykonane <em>({done.length})</em>
				</h3>
				{done.length === 0 ? <p>Brak zadań.</p> : null}
				{done.length > 5 && <span>Pięć ostatnio wykonanych zadań:</span>}
				{doneTasks.slice(0, 4)}
			</div>
		</div>
	);
};

export default TaskList;
